<template>
  <div>
    <md-table v-model="cars" :table-header-color="tableHeaderColor">
      <md-table-row
        slot="md-table-row"
        slot-scope="{ item }"
        md-auto-select
        md-selectable="multiple"
      >
        <md-table-cell md-label="Bin s xe ">{{ item.c_plate }}</md-table-cell>
        <md-table-cell md-label="Imei thit b">{{ item.d_IMEI }}</md-table-cell>
        <md-table-cell md-label="Thao tc">
          <md-button class="md-raised edit_btn">Sa</md-button>
          <md-button class="md-raised delete_btn" @click="active=true;selectedRecord=item">Xa</md-button>
        </md-table-cell>
      </md-table-row>
    </md-table>
    <md-dialog :md-active.sync="showDialogProp">
      <md-dialog-title>Thm thng tin xe</md-dialog-title>
      <md-content>
        <div>
          <md-field>
            <label>Bin s</label>
            <md-input v-model="plate"></md-input>
          </md-field>
          <md-field>
            <label>IMEI Thit b</label>
            <md-input v-model="imei"></md-input>
          </md-field>
        </div>
      </md-content>
      <md-dialog-actions>
        <md-button class="md-primary" @click="$emit('hideDialog', false)">Hy</md-button>
        <md-button class="md-primary" @click="hideDiaLog()">Lu</md-button>
      </md-dialog-actions>
    </md-dialog>
    <md-dialog-confirm
      :md-active.sync="active"
      md-title="Xa?"
      md-content="Bn c chc chn mun xa hay khng?"
      md-confirm-text="ng "
      md-cancel-text="Hy"
      @md-confirm="onConfirm"
    />
  </div>
</template>

<script>
import { getAllCar, insertCar, deleteCar } from "../../api/car";
export default {
  name: "simple-table",
  props: {
    tableHeaderColor: {
      type: String,
      default: ""
    },
    showDialog: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      selected: [],
      cars: [],
      imei: "",
      plate: "",
      active: false,
      selectedRecord: {}
    };
  },
  methods: {
    getList() {
      this.cars = [];
      getAllCar().then(result => {
        this.cars = result.data;
        this.cars.forEach(function(value, index) {
          value.id = index + 1;
        });
      }).catch(err=>{
        console.log(err)
      })
    },
    onConfirm() {
      deleteCar(this.selectedRecord)
        .then(() => {
          this.$message({
            message: "Xa thnh cng",
            type: "success"
          });
        })
        .then(() => {
          this.getList();
        });
    },
    hideDiaLog() {
      insertCar({ imei: this.imei, plate: this.plate }).then(result => {
        if (result.status === 200) {
          this.$message({
            message: "Thm thnh cng",
            type: "success"
          });
          this.getList();
        } else {
          this.$message({
            message: "Xy ra li",
            type: "error"
          });
        }
      });
      this.$nextTick(() => {
        this.$emit("hideDialog", false);
      });
    }
  },
  mounted() {
    getAllCar().then(result => {
      this.cars = result.data;
      this.cars.forEach(function(value, index) {
        value.id = index + 1;
      });
    });
  },
  computed: {
    showDialogProp: {
      get() {
        return this.showDialog;
      },
      set() {
        return !this.showDialog;
      }
    }
  }
};
</script>
<style lang="scss" scoped>
.md-dialog {
  max-width: 732px;
  min-width: 400px;
}
.delete_btn {
  background: #ff5252 !important;
}
.edit_btn {
  background: #448aff !important;
}
</style>
